"use client";

import { FormEvent, useState } from "react";

export default function FamiliesPage() {
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [location, setLocation] = useState("");
  const [shiftPattern, setShiftPattern] = useState("rotating");
  const [kidsInfo, setKidsInfo] = useState("");
  const [supportWindow, setSupportWindow] = useState("");
  const [notes, setNotes] = useState("");
  const [submitted, setSubmitted] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = (event: FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    setError(null);
    setSubmitted(false);

    if (!fullName || !email || !location || !kidsInfo || !supportWindow) {
      setError("Please fill in the required fields.");
      return;
    }

    console.log("Family onboarding", {
      fullName,
      email,
      location,
      shiftPattern,
      kidsInfo,
      supportWindow,
      notes,
    });

    setSubmitted(true);
  };

  return (
    <section className="families-auth">
      <div className="families-auth-inner">
        <div className="families-hero">
          <p className="eyebrow">Families</p>
          <h1>Join ShiftSitter as a parent</h1>
          <p className="lead">
            Tell us about your schedule, your kids, and the kind of support you need.
            We will line you up with nearby families for reciprocal care that works
            for shifts, nights, weekends, and rotating weeks.
          </p>
          <ul className="families-bullets">
            <li>Verified community with ID checks and location boundaries.</li>
            <li>Matches based on shift patterns, proximity, and comfort preferences.</li>
            <li>We guide introductions before any swap so everyone feels prepared.</li>
          </ul>
        </div>

        <div className="families-card">
          <div className="card-header">
            <h2>Parent onboarding form</h2>
            <p className="muted">
              Share the essentials so we can create your onboarding record and queue
              up your first matches. No payment is needed to submit this form.
            </p>
          </div>

          <form onSubmit={handleSubmit} className="form-stack">
            <div className="form-field">
              <label htmlFor="fullName">Full name</label>
              <input
                id="fullName"
                type="text"
                className="ss-input"
                placeholder="Taylor Morgan"
                value={fullName}
                onChange={(e) => setFullName(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="email">Email</label>
              <input
                id="email"
                type="email"
                className="ss-input"
                placeholder="you@example.com"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="location">City and ZIP</label>
              <input
                id="location"
                type="text"
                className="ss-input"
                placeholder="Baltimore, 21201"
                value={location}
                onChange={(e) => setLocation(e.target.value)}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="shiftPattern">Primary shift pattern</label>
              <select
                id="shiftPattern"
                className="ss-input"
                value={shiftPattern}
                onChange={(e) => setShiftPattern(e.target.value)}
              >
                <option value="rotating">Rotating shifts</option>
                <option value="nights">Nights</option>
                <option value="weekends">Weekends</option>
                <option value="early">Early mornings</option>
                <option value="standard">Standard daytime</option>
              </select>
            </div>

            <div className="form-field">
              <label htmlFor="kidsInfo">Kids and ages</label>
              <textarea
                id="kidsInfo"
                className="ss-input"
                placeholder="Example: Ava (4), Lucas (7)"
                value={kidsInfo}
                onChange={(e) => setKidsInfo(e.target.value)}
                rows={2}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="supportWindow">Coverage windows needed</label>
              <textarea
                id="supportWindow"
                className="ss-input"
                placeholder="Example: Mon-Thu 6:30pm-12am, every other Saturday 7am-3pm"
                value={supportWindow}
                onChange={(e) => setSupportWindow(e.target.value)}
                rows={3}
                required
              />
            </div>

            <div className="form-field">
              <label htmlFor="notes">Notes or care preferences (optional)</label>
              <textarea
                id="notes"
                className="ss-input"
                placeholder="Transportation limits, allergies, preferred introductions, etc."
                value={notes}
                onChange={(e) => setNotes(e.target.value)}
                rows={3}
              />
            </div>

            {error && <p className="form-error">{error}</p>}
            {submitted && (
              <p
                style={{
                  margin: "4px 0 12px",
                  color: "#0f766e",
                  fontWeight: 700,
                }}
              >
                Thanks for sharing your details. You are in the matching queue and
                we will email you next steps.
              </p>
            )}

            <div className="actions">
              <button type="submit" className="ss-btn w-100">
                Submit my details
              </button>
              <p className="muted" style={{ margin: 0, fontSize: "0.95rem" }}>
                We only use this info to verify and coordinate your matches. You can
                update it anytime during onboarding.
              </p>
            </div>
          </form>
        </div>
      </div>
    </section>
  );
}
